<template>
    <yd-layout>
        <yd-navbar bgcolor="#70b6ef" title="协议酒店查询" color="#fff" fontsize="0.36rem">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon color="#fff">返回</yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="mainContentQuery">
            <div class="contentSradius querySradius">
                <yd-cell-item>
                    <input slot="right" readonly="true" v-on:click="bottomPopup = true;statusRadio=statusRadioFrom;isType=0" :value='hotelCity' type="text" placeholder="请输入城市">
                </yd-cell-item>
                <yd-cell-item>
                    <input slot="right" type="text" placeholder="请输入酒店名称" v-model="hotelName">
                </yd-cell-item>
            </div>
            <yd-button size="large" type="primary" bgcolor="#00B6FC" color="#fefefe" v-on:click.native="btnQuery()">搜索</yd-button>
        </div>
        <div class="mainContentFluid queryPath pubList" v-if="btnNum == 1">
            <yd-flexbox v-for="item in hotelList" :key="item.hotelName">
                <yd-flexbox-item>
                    <div class="tit" v-text="item.hotelName"></div>
                    <div class="add">
                        <div class="hotelAddress">地址：{{item.address}}</div>
                        <a v-bind:href="'http://api.map.baidu.com/geocoder?address='+item.address+'&amp;output=html&amp;zoom&amp;src=yourCompanyName|yourAppName'">
                            <img src="../../../static/images/query/q_6.jpg" class="addPhoto" />
                        </a>
                    </div>
                    <div class="tel">电话：{{item.telephone}}<a v-bind:href="'tel:'+item.telephone"><img src="../../../static/images/query/q_7.png" class="telPhoto" /></a></div>
                </yd-flexbox-item>
            </yd-flexbox>
        </div>
        <div class="mainContentFluid queryPath pubList" v-if="btnNum == 2">
            <yd-flexbox v-for="item in searchList" :key="item.hotelName">
                <yd-flexbox-item>
                    <div class="tit" v-text="item.hotelName"></div>
                    <div class="add">
                        <div class="hotelAddress">地址：{{item.address}}</div>
                        <a href="">
                            <img src="../../../static/images/query/q_6.jpg" class="addPhoto" />
                        </a>
                    </div>
                    <div class="tel">{{item.telephone}}<a href="" v-bind:href="'tel:'+item.telephone"><img src="../../../static/images/query/q_7.png" class="telPhoto"/></a></div>
                </yd-flexbox-item>
            </yd-flexbox>
        </div>
        <div class="mainContentFluid queryPath pubList" v-if="btnNum == 3">
            <div class="noResults">无搜索结果</div>
        </div>
        <yd-popup v-model="bottomPopup" position="bottom" height="4rem">
            <div class="btnPopup" slot="top">
                <div class="cancel" v-on:click="bottomPopup = false;statusRadio=999">取消</div>
                <div class="confirm" @click="confirm">确定</div>
            </div>
            <div class="selectStatus">
                <div>
                    <ul class="ul">
                        <li class="li" v-for="(item,index) in popupArr" v-bind:class="{active:statusRadio==index}" v-on:click="statusRadio=index;isType==0?hotelCity=item:toOpt=item;isType == 0?statusRadioFrom =statusRadio :statusRadioTo =statusRadio" v-text='item'></li>
                    </ul>
                </div>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
export default {
    name: 'hotel',
    data() {
        return {
            btnNum: 1,
            hotelList: [],
            searchList: [],
            hotelCity: '上海',
            hotelName: '',
            bottomPopup: false,
            popupArr: [],
            isType: 0,
            statusRadio: 999,
            statusRadioFrom: 999,
            statusRadioTo: 999
        }
    },
    mounted() {
        document.setTitle('协议酒店查询');
        this.initData();
    },
    methods: {
        confirm: function (event) {
            var vm = this;
            vm.bottomPopup = false;
            vm.isType == 0 ? vm.statusRadioFrom = vm.statusRadio : vm.statusRadioTo = vm.statusRadio;
        },
        initData() {
            var vm = this;
            vm.get(vm.conf.wcm + '/app/hotel/index.json', {},
                function (res) {
                    vm.hotelList = res.jsonData;
                    vm.popupArr = res.provinceList;
                    vm.btnQuery();
                });
        },
        btnQuery() {
            var vm = this;
            if (vm.hotelCity == '' & vm.hotelName == '') {
                vm.$dialog.toast({
                    mes: '请输入城市或者酒店名称',
                    timeout: 1000
                });
            } else {
                vm.btnNum = 2;
                vm.searchList = [];
                if (vm.hotelName == '') {
                    for (var i = 0; i < vm.hotelList.length; i++) {
                        var item = vm.hotelList[i];
                        if (item.province == vm.hotelCity) {
                            vm.searchList.push(item);
                        }
                    }
                } else if (vm.hotelCity == '') {
                    for (var j = 0; j < vm.hotelList.length; j++) {
                        var item2 = vm.hotelList[j];
                        if (item2.hotelName.indexOf(vm.hotelName) != -1) {
                            vm.searchList.push(item2);
                        }
                    }
                } else {
                    for (var k = 0; k < vm.hotelList.length; k++) {
                        var item3 = vm.hotelList[k];
                        if (item3.province == vm.hotelCity && item3.hotelName.indexOf(vm.hotelName) != -1) {
                            vm.searchList.push(item3);
                        }
                    }
                }
                if (vm.searchList.length == 0) {
                    vm.btnNum = 3;
                }
            }
        }
    }
}
</script>
<style scoped>
.mainContentFluid {
    margin-top: .17rem;
}

.add,
.time,
.tel,
.tit {
    padding-left: .45rem;
}

.yd-flexbox,
.yd-flexbox-item {}

.queryPath .yd-flexbox {
    height: 2.37rem;
}

.yd-flexbox-item {
    padding: .2rem 0;
}

.addPhoto,
.telPhoto {
    width: .28rem;
    position: absolute;
    top: .19rem;
    right: .36rem;
}

.noResults {
    font-size: 0.28rem;
    text-align: center;
    padding: 0.2rem 0;
}

.hotelAddress {
    line-height: 0.5rem;
    width: 5.9rem;
}
</style>


